import data from './data';
import Animate from './animate';
import Legend from './legend';

function Jobubbles() {
    let svg = d3.select("svg");
    let width = document.body.clientWidth; 
    let height = +svg.attr("height");
    let centerX = width * 0.55;
    let centerY = height * 0.5;
    let focusedNode;

    let scaleColor = d3.scaleOrdinal([`#789c6e`, `#4c5f72`, `#1e3349`, `#a64960`, `#29586c`, `#1d456d`, `#85888b`, `#a9845c`, `#89c7d6`])

    let simulation = d3
        .forceSimulation()
        .force("charge", d3.forceManyBody())
        .force("collide", d3.forceCollide(d => d.r))
        .force("x", d3.forceX(centerX))
        .force("y", d3.forceY(centerY));

    let pack = d3.pack().size([width, height]).padding(2);
    let root = d3.hierarchy({ children: data }).sum(d => d.value);

    let nodes = pack(root)
        .descendants() 
        .filter(function (d) {
            return d.depth === 1;
        })
        .map(node => {
            const data = node.data;
            return {
                x: centerX + (node.x - centerX) * 3,
                y: centerY + (node.y - centerY) * 3,
                r: 0,
                radius: node.r,
                id: data.cat + "." + data.name.replace(/\s/g, "-"),
                cat: data.cat,
                name: data.name,
                value: data.value,
                icon: data.icon,
                desc: data.desc,
                link: data.link,
                linkUrl: data.linkUrl,
                company: data.company,
                companyUrl: data.companyUrl
            };
        });

    simulation.nodes(nodes).on("tick", ticked);

    let node = svg
        .selectAll(".node")
        .data(nodes)
        .enter()
        .append("g")
        .attr("class", "node")
        .call(
            d3
                .drag()
                .on("start", d => {
                    if (!d3.event.active) simulation.alphaTarget(0.2).restart();
                    d.fx = d.x;
                    d.fy = d.y;
                })
                .on("drag", d => {
                    d.fx = d3.event.x;
                    d.fy = d3.event.y;
                })
                .on("end", d => {
                    if (!d3.event.active) simulation.alphaTarget(0);
                    d.fx = null;
                    d.fy = null;
                })
        );

    node
        .append("circle")
        .attr("id", d => d.id)
        .attr("r", 0)
        .style("fill", d => scaleColor(d.cat)) 
        .style("opacity", 0.7)
        .transition()
        .duration(300)
        .tween("circleIn", d => {
            let i = d3.interpolateNumber(0, d.radius);
            return t => {
                d.r = i(t);
                simulation.force("collide", d3.forceCollide(d => d.r));
            };
        });

    node
        .append("clipPath")
        .attr("id", d => `clip-${d.id}`)
        .append("use")
        .attr("xlink:href", d => `#${d.id}`);

    node
        .filter(d => !String(d.name))
        .append("text")
        .classed("node-icon", true)
        .attr("clip-path", d => `url(#clip-${d.id})`)
        .selectAll("tspan")
        .data(d => d.icon.split(";"))
        .enter()

    node
        .filter(d => String(d.name))
        .append("image")
        .classed("node-icon", true)
        .attr("clip-path", d => `url(#clip-${d.id})`)
        .attr("xlink:href", d => d.icon)
        .attr("x", d => -d.radius * 0.9)
        .attr("y", d => -d.radius * 0.9)
        .attr("height", d => d.radius * 2 * 0.9)
        .attr("width", d => d.radius * 2 * 0.9);

    Legend(scaleColor, svg);

    let infoBox = node
        .append("foreignObject")
        .classed("circle-overlay hidden", true)
        .attr("x", -550 * 0.5 * 0.8) 
        .attr("y", -200 * 0.5 * 0.8)
        .attr("height", 350 * 0.8) 
        .attr("width", 550 * 0.8) 
        .append("xhtml:div")
        .classed("circle-overlay__inner", true);

    infoBox
        .append("h2")
        .classed("circle-overlay__title", true)
        .text(d => d.name);

    Animate(node, focusedNode, simulation, centerX, centerY);

    function ticked() {
        node
            .attr("transform", d => `translate(${d.x},${d.y})`)
            .select("circle")
            .attr("r", d => d.r);
    }

}

document.addEventListener('DOMContentLoaded', () => {
    Jobubbles();
})
